import React, { Component } from "react";
import { Typography } from "@material-ui/core";

export class SubTitle extends Component {
  render() {
    const { children } = this.props;
    return (
      <Typography variant="subtitle1" gutterBottom color="inherit">
        {children}
      </Typography>
    );
  }
}



