import React, { Component } from "react";
import { Typography } from "@material-ui/core";

export class Title extends Component {
  render() {
    const { children, className } = this.props;
    return (
      <Typography
        variant="h6"
        gutterBottom
        color="inherit"
        className={className}
      >
        {children}
      </Typography>
    );
  }
}



