import React, { Component } from "react";
import { AvatarStyled } from "./avatar.style";

export class Avatar extends Component {
  render() {
    return (
      <AvatarStyled
        src={
          "https://pbs.twimg.com/profile_images/1038494743729201154/vpl6_GgH_400x400.jpg"
        }
      />
    );
  }
}



