import React, { Component } from "react";
import { Button } from "@material-ui/core";
import { Container, Card, Title, SubTitle } from "../../components";
import {
  TwitterSquareIcon,
  GithubCircleIcon,
  LinkedInCircleIcon
} from "../../icons";

export class ProfileCard extends Component {
  render() {
    return (
      <Card>
        <Container direction="column" align="center" justify="center">
          <img
            src="https://pbs.twimg.com/profile_images/1038494743729201154/vpl6_GgH_400x400.jpg"
            style={{ borderRadius: "50%", width: "35%" }}
          />
          <Title>Juan Alberto Palavecino</Title>
          <SubTitle>FullStack Developer</SubTitle>
          <Container>
            <Button color="primary" href="https://twitter.com/Leorek">
              <TwitterSquareIcon style={{ fontSize: 36 }} />
            </Button>
            <Button color="primary" href="https://github.com/Leorek">
              <GithubCircleIcon style={{ fontSize: 36 }} />
            </Button>
            <Button
              color="primary"
              href="https://www.linkedin.com/in/leorek93/"
            >
              <LinkedInCircleIcon style={{ fontSize: 36 }} />
            </Button>
          </Container>
        </Container>
      </Card>
    );
  }
}



